@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&400&200display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
td {
  background-color: white;
  padding: 5px;
  text-align: left;
  border: 0.5 solid white;
}

tr {
  border: 0;
}

.actions {
  text-align: center;
  background-color: #e37f4d;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

th {
  background-color: #02182b;
  color: white;
  padding: 10px;
}

table {
  width: 100%;
  box-shadow: 5px 5px 15px -5px #000000;
}

.title-name {
  color: #02182b;
  text-align: left;
}

.Dashboard-container {
  background-color: #02182b;
  width: 95%;
  height: 95%;
  border-radius: 10px;
  box-shadow: 5px 5px 15px -5px #000000;
}

.Dashboard-Header {
  color: #e37f4d;
}

.Dashboard-inset {
  margin-left: 5%;
}

.Dashboard-Header {
  font-weight: 600;
}

.sub-header {
  color: #e37f4d;
}

.Category-header {
  color: white;
  display: flex;
  justify-content: space-between;
  width: 60%;
  font-size: 15px;
  margin-top: 1%;
}

.nav {
  color: white;
}

.logo h1 {
  margin-top: 5%;
  font-size: 30px;
  margin-left: 5%;
  word-wrap: break-word;
  color: white;
}

.logo h2 {
  font-size: 15px;
  margin-left: 5%;
  color: #e37f4d;
}

.nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.nav ul li {
  margin-top: 10%;
  /* margin-left: 10%; */
  margin-left: 5%;
  padding: 5px;
  font-size: 15px;
  list-style: none;
}

.nav ul li a {
  color: white;
  text-decoration: none;
}

.nav ul li a:hover {
  cursor: pointer;
  color: #e37f4d;
}

@media only screen and (max-width: 768px) {
  /* Para mi modelo responsivo: navigation-bar*/
  .nav ul {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .nav ul li {
    margin: 1%;
  }

  .nav ul li:nth-child(6) {
    order: 5;
  }
}

.Notifiicaciones {
  color: #e37f4d;
  font-weight: 400;
  margin-top: 5%;
  margin-left: 5%;
  margin-right: 5%;
}

/* .show-all {
  margin-top: 5%;
  margin-left: 5%;
  margin-right: 5%;
  padding: 5px;
  border-radius: 3px;
  border: 0;
  color: white;
  background-color: #e37f4d;
  width: 90%;
} */

.Notification-messages {
  overflow-y: scroll;
  height: 600px;
  margin-left: 5%;
  margin-right: 5%;
  background-color: white;
  display: flex;
  flex-direction: column;
  border: 3px solid black;
}

.Message {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 12px;
  /* border: 3px solid black; */
  font-weight: 400;
  border-top: 2px solid #d4d4d4;
}

.Message:hover {
  background-color: #e37f4d;
  color: white;
}

@media only screen and (max-width: 768px) {
  /* Para mi modelo responsivo: page-viewer */
  .Notification-messages {
    height: 150px;
    justify-content: space-evenly;
    overflow-y: scroll;
  }

  .Message {
    margin-left: 1%;
  }

  .Notifications {
    height: 350px;
  }

  .message-element {
    flex: 1 1;
  }
  .message-text {
    flex: 2 1;
  }
}


.createInventory-form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #02182b;
  color: white;
  font-weight: 600;
  border-radius: 5px;
  box-shadow: 5px 5px 15px -5px #000000;
}

.title-header {
  color: #02182b;
}

.form-input {
  font-size: 13px;
  font-weight: 400;
  margin: 10px;
  display: flex;
  justify-content: space-between;
}

.form-input input[type="text"],
input[type="number"] {
  border: 0;
  border-radius: 5px;
  text-indent: 5px;
}

.submit-form {
  border: 0;
  padding: 1px;
  border-radius: 5px;
  color: white;
  background-color: #e37f4d;
  width: 100%;
  box-shadow: 5px 5px 15px -5px #000000;
}

.clientList {
  display: flex;
  font-size: 10px;
  flex-direction: column;
  justify-content: space-between;
}

.client {
  margin-top: 1%;
  border: 3px solid #02182b;
  background-color: #02182b;
  color: white;
  border-radius: 10px;
}

.client-content {
  margin-left: 1%;
}

.order-listing {
  background-color: #02182b;
  margin-top: 4%;
}

.order-listing p {
  font-size: 12px;
}

.master {
  font-size: 15px;
  color: white;
}

.master-content {
  margin-left: 2%;
}

.detail {
  background-color: white;
}

.individual-detail {
  border-bottom: 0.5px solid grey;
}

.individual-detail p {
  margin-left: 3%;
}

.total p {
  color: white;
}

.form-content {
  background-color: #02182b;
  color: white;
  padding: 5px;
  border-radius: 10px;
  box-shadow: 5px 5px 15px -5px #000000;
}

.form-submit {
  background-color: #e37f4d;
  color: white;
  padding: 5px;
  border: 0;
  border-radius: 15px;
  cursor: pointer;
}

.form-submit:hover {
  border: 3px solid white;
}

.form-inputs {
  margin: 10px auto;
  margin-left: 5%;
  margin-right: 5%;
}

.big-input,
.category-input {
  resize: none;
  width: 300px;
  border-radius: 5px;
  border: 0;
  text-indent: 5px;
}

.big-input:hover,
.category-input:hover {
  border: 3px solid #e37f4d;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  font-weight: 400;
}

html,
body {
  height: 100%;
  overflow-y: hidden;
}

.Page-wrapper {
  display: flex;
}

.Page-view {
  flex: 5 1;
  background-color: #d4d4d4;
}

.Notifications-bar {
  flex: 1 1;
  background-color: #02182b;
}

.Navigation-bar {
  flex: 1 1;
  min-width: 150px;
  background-color: #02182b;
  height: 1200px;
}

.Viewer {
  overflow-y: scroll;
  margin: 30px auto;
  margin-left: 5%;
  margin-right: 5%;
  /* min-height: 550px;
  max-height: 550px; */
  height: 550px;
}

@media only screen and (max-width: 768px) {
  /* Para mi modelo responsivo: page-viewer */
  .Page-wrapper {
    flex-direction: column;
  }

  .Page-wrapper :nth-child(2) {
    order: 3;
  }

  .html,
  body {
    height: 100%;
    overflow-y: scroll;
  }
}

